body {
  margin: 0;
  padding: 0;
  color: #35322D;
  background-color: #595756;
  min-width: 320px;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, p, dl, dd {
  margin: 0;
}

input {
  border-radius: 0;
  -webkit-appearance: none;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

@media screen and (-webkit-min-device-pixel-ratio:0) { 
  select,
  textarea,
  input {
    font-size: 16px;
  }
}
div.App {
  position: relative;
}
header.app-header {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.8);
    width: 100vw;
    height: 40px;
    top: 0px;
    left: 0px;
    z-index: 100;
    border-bottom: 1px solid rgba(0,0,0,0.14);
    -webkit-backdrop-filter: blur(7px);
            backdrop-filter: blur(7px);
}

header.app-header img.logo {
    position: relative;
    display: block;
    margin: 0 auto;
    width: 10px;
    height: 16px;
    background-size: 10px 16px;
}


div.card {
    background-color: #FFFFFF;
    margin: 20px;
    margin-bottom: 25px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0px 6px 40px rgba(0, 0, 0, 0.2), 
                0px 1px 3px rgba(0, 0, 0, 0.1);
}

div.card div.card-top {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
}

div.card div.card-top p.username{
   margin-left: 7px;
   font-weight: 700;
   font-size: 14px;
}

div.card div.card-top img.profile-pic {
    width: 30px;
    height: 30px;
    background-size: 30px 30px;
    border-radius: 50%;
}

div.card p.card-text {
    font-size: 14px;
    font-weight: 400;
    line-height: 23px;
    margin-bottom: 15px;
}

div.card div.card-bottom {
    border-top: 1px solid #F0EDEC;
    padding-top: 15px;
}

div.card div.card-bottom div.likes {
    display: flex;
}

div.card div.card-bottom div.likes img {
    margin-right: 5px;
}

div.card div.card-bottom div.likes p {
    font-size: 12px;
    color: #63605F;
}
