footer.app-footer {
    position: fixed;
    display: flex;
    background-color: rgba(255, 255, 255, 0.8);
    width: 100vw;
    height: 55px;
    bottom: 0px;
    left: 0px;
    z-index: 100;
    border-top: 1px solid rgba(0,0,0,0.14);
    -webkit-backdrop-filter: blur(7px);
            backdrop-filter: blur(7px);
}

footer.app-footer nav {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0 5%;
}

footer.app-footer nav ul.nav-buttons {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    padding: 0;
    margin: 0;
    list-style-type: none;
}

footer.app-footer nav ul.nav-buttons li {
    display: flex;
    justify-content: center;
    align-items: center;
}

